<!-- style -->
<style lang="less">
@import (reference) '../common/css/common';
@module: c-artice-better;

.@{module} {
    & &-wrap {
        width: 100%;
        height: 100%;
        margin-bottom: 30px;
    }
    & .@{module}-item {
        width: 100%;
        height: 100%;
        padding: 20px;
        .default_backgroud_2;
        .default_border-b-14; 
    }
    & .@{module}_content {
        & .content__title {
            margin-bottom: 10px;
            .default_font_size_7;
            .default_color_1;
            .default_font_family_bolder;
        }
        & .content__time {
            margin-bottom: 15px;
            .default_font_size_1;
            .default_color_3;
        }
        & .content__img {
            width: 640px;
            height: 320px;
            overflow: hidden;
            margin-bottom: 20px;
            & img {
                width: 100%;
            }
        }
        & .content__text {
            width: 100%;
            line-height: 1.5rem;
            margin-bottom: 15px;
            .default_font_size_2;
            .default_color_2;
        }
    }
    & .@{module}_bottom {
        & .bottom__link {
            .default_font_size_2;
            .default_color_5;
        }
    }
}
</style>

<!-- html -->
<template>
<div class="c-artice-better">
<div class="c-artice-better-wrap">
    <div class="c-artice-better-item" v-for="item in lists">
        <div class="c-artice-better_content">
            <div class="content__title">{{item.title}}</div>
            <div class="content__time">{{item.time}}</div>
            <div class="content__img"><img :src="item.img" /></div>
            <div class="content__text">{{item.text}}</div>
        </div>
        <div class="c-artice-better_bottom">
            <a class="bottom__link" href="#none"></a>
        </div>
    </div>
    <Empty v-if="count <= 0" />
</div>
<Page 
    :count="count" 
    :length="pageSize"
    :index="pageIndex"
    @page_switch="pageSwitch"
/>
</div>
</template>

<script>
import img1 from '../common/images/discover/img-1.png'
export default {
    data (){
        return {
            count: 4,
            lists: [
                {
                    name: '',
                    time: '2',
                    img: img1,
                    title: '',
                    text: '...'
                },
                {
                    name: '',
                    time: '2',
                    img: img1,
                    title: '',
                    text: '...',
                    tags: [''],
                    fabulous: 123,
                    comment: 8134
                },
                {
                    name: '',
                    time: '2',
                    img: img1,
                    title: '',
                    text: '...',
                    tags: [''],
                    fabulous: 123,
                    comment: 8134
                }
            ],

            pageIndex: 1,
            pageSize: 5,
            messageType: 1
        }
    },
    methods: {
        pageSwitch (pageIndex){
            if (this.pageSwitch == pageIndex) {
                return false;
            }
            this.pageSwitch = pageIndex;
        },
        getMessageList (){
            this.$store.dispatch('message_getMessageList', {
                "page": this.pageIndex,
                "pageSize": this.pageSize,
                "pagination": 1,
                "messageType": this.messageType
            }).then( info => {
                this.$store.dispatch('bubble_success', info);
            }).catch(err => {
                this.$store.dispatch('bubble_fail', err);
            });
        }
    },
    mounted (){
        // 
        this.getMessageList();
    }
}
</script>




