	.@{bubble}-declaration {
		position: fixed;
		top: 50%;
		left: 50%;
		z-index: 30;
		width: 720px;
		height: 540px;
		margin: -270px 0 0 -360px;
		.default_backgroud_2;
		& .declaration-title {
			width: 100%;
			height: 60px;
			line-height: 60px;
			padding-left: 30px;
			.default_font_size_3;
			.default_color_3;
			.default_border-b-5;
		}
		& .declaration-content {
			width: 100%;
			height: 400px;
			padding: 40px 50px;
			overflow-y: auto;
			.default_backgroud_4;
			.default_font_size_3;
			.default_color_2;
			& h3 {
				margin-bottom: 30px;
				line-height: 1.5rem;
				.default_font_size_3;
				.default_color_2;
			}
			& p {
				line-height: 1.5rem;
			}
		}
		& .declaration-bottom {
			width: 100%;
			height: 80px;
			padding-top: 20px;
			.default_border-t-5;
			.default_center;
			& .declaration-bottom-cancel {
				margin-right: 60px;
			}
		}
	}
	.@{bubble}-warn {
		position: fixed;
		top: 50%;
		left: 50%;
		z-index: 30;
		width: 300px;
		transform: translate(-50%, -50%);
		.default_backgroud_2;
		.default_border-r-4;
		& .warn-title {
			padding-top: 30px;
			margin-bottom: 20px;
			text-align: center;
			.default_color_7;
			.default_font_bolder;
			.default_font_size_4;
		}
		& .warn-content {
			padding: 0 30px;
			line-height: 1.5rem;
			margin-bottom: 30px;
			.default_color_2;
			.default_font_size_2;
			& p {
				line-height: 1.5rem;
				margin-bottom: 20px;
			}
		 	& a {
		 		line-height: 1.5rem;
				.default_font_size_1;
				.default_color_3;
				&:hover {
					.default_color_1;
				}
		 	}
		}
		& .warn-bottom {
			width: 100%;
			height: 50px;
			padding: 10px 0;
			overflow: hidden;
			.default_backgroud_7;
			& .warn-bottom-item {
				float: left;
				width: 50%;
				height: 30px;
				line-height: 30px;
				text-align: center;
				.default_color_2;
				.default_font_size_2;
				.default_pointer;
				&:hover {
					.default_color_1;
				}
			}
			& .warn-bottom-cancel {
