	.@{bubble}-complete {
		position: fixed;
		top: 50%;
		left: 50%;
		z-index: 30;
		width: 460px;
		transform: translate(-50%, -50%);
		.default_backgroud_2;
		.default_border-r-4;
		& .complete-title {
			padding-top: 30px;
			margin-bottom: 15px;
			text-align: center;
			.default_color_7;
			.default_font_size_6;
		}
		& .complete-content {
			padding: 0 30px;
			line-height: 1.5rem;
			margin-bottom: 30px;
			.default_color_2;
			.default_font_size_2;
			.default_center;
			& p {
				line-height: 1.5rem;
				margin-bottom: 20px;
				&:last-child {
					margin-bottom: 15px;
				}
			}
		 	& .complete-share {
		 		width: 100%;
		 		overflow: hidden;
		 		.default_center;
		 		& .share-item {
		 			display: inline-block;
		 			width: 30px;
		 			height: 30px;
		 			margin-right: 10px;
		 			.default_border-r-50;
		 			.default_middle;
		 			.default_backgroud_7;
		 			.default_pointer;
		 			&:last-child {
		 				margin-right: 0;
		 			}
		 		}
		 		& .share-py {
					.skin_icon_complete-1;
					&:hover {
						.skin_icon_complete-1_on;
					}
		 		}
		 		& .share-line {
					.skin_icon_complete-2;
					&:hover {
						.skin_icon_complete-2_on;
					}
		 		}
		 		& .share-qq {
					.skin_icon_complete-3;
					&:hover {
						.skin_icon_complete-3_on;
					}
		 		}
		 		& .share-kong {
					.skin_icon_complete-4;
					&:hover {
						.skin_icon_complete-4_on;
					}
		 		}
		 		& .share-wb {
					.skin_icon_complete-5;
					&:hover {
						.skin_icon_complete-5_on;
					}
		 		}
		 		& .share-more {
					.skin_icon_complete-6;
					&:hover {
						.skin_icon_complete-6_on;
					}
		 		}
		 	}
		}
		& .complete-bottom {
			width: 100%;
			height: 50px;
			padding: 10px 0;
			overflow: hidden;
			.default_backgroud_7;
			& .complete-bottom-item {
				float: left;
				width: 50%;
				height: 30px;
				line-height: 30px;
				text-align: center;
				.default_color_2;
				.default_font_size_2;
				.default_pointer;
				&:hover {
