</style>

<!-- html -->
<template>
<div id="bubble" class="c-bubble" :class="{cpm_hide: !data.show}">
	<div class="c-bubble-mask" @click="close"></div>
	<!--  -->
    <div v-if="data.type == 'top'" class="c-bubble-top" :class="data.top.status">

    	{{data.top.msg}}
    </div>

    <!--  -->
    <div v-if="data.type == 'report'" class="c-bubble-report">
    	<div class="report-title">
    		
    	</div>
    	<div class="report-content">
    		<div class="select-item z-on" data-type="adv" @click="radioSwitch">
    			<span class="select-item-radio"><em></em></span>
    			<span class="select-item-text"></span>
    		</div>
    		<div class="select-item" data-type="copy" @click="radioSwitch">
    			<span class="select-item-radio"><em></em></span>
    			<span class="select-item-text"></span>
    		</div>
    		<div class="select-item" data-type="other" @click="radioSwitch">
    			<span class="select-item-radio"><em></em></span>
    			<span class="select-item-text"></span>
    		</div>
    		<div class="textare-item">
    			<textarea placeholder="" v-model="content"></textarea>
    		</div>
    	</div>
    	<div class="report-bottom">
    		<div class="report-bottom-item report-bottom-cancel" @click="close"></div>
    		<div class="report-bottom-item report-bottom-comfire" @click="addReport"></div>
    	</div>
    </div>

    <!--  -->
    <div v-if="data.type == 'declaration'" class="c-bubble-declaration">
    	<div class="declaration-title"></div>
    	<div class="declaration-content">
    		<h3></h3>
			<p></p>
			<p>1. </p>
			<p>2. </p>
			<p>3. </p>
			<p>4. </p>
			<p>5. </p>
			<p>6. </p>
			<p>7. </p>
			<p>8. </p>
    	</div>
    	<div class="declaration-bottom">
    		<div class="declaration-bottom-cancel cpm_button_weak z-disline"></div>
    		<div class="declaration-bottom-comfire cpm_button_default z-disline"></div>
    	</div>
    </div>

    <!-- dialog -->
    <div v-if="data.type == 'warn'" class="c-bubble-warn">
    	<div class="warn-title">{{data.warn.title}}</div>
    	<div v-if="data.warn.html" class="warn-content" v-html="data.warn.html"></div>
    	<div v-else class="warn-content">{{data.warn.content}}</div>
    	<div class="warn-bottom">
    		<div class="warn-bottom-item warn-bottom-cancel" @click="close"></div>
    		<div class="warn-bottom-item warn-bottom-comfire" @click="comfire"></div>
    	</div>
    </div>

    <!--  -->
    <div v-if="data.type == 'complete'" class="c-bubble-complete">
    	<div class="complete-title">{{data.complete.title}}</div>
    	<div class="complete-content">
    		<p></p>
    		<p></p>
    		<div class="complete-share">
    			<div class="share-item share-py"></div>
    			<div class="share-item share-line"></div>
    			<div class="share-item share-qq"></div>
    			<div class="share-item share-kong"></div>
    			<div class="share-item share-wb"></div>
    			<div class="share-item share-more"></div>
    		</div>
    	</div>
    	<div class="complete-bottom">
    		<div class="complete-bottom-item complete-bottom-cancel" @click="close"></div>
    		<div class="complete-bottom-item complete-bottom-comfire" @click="comfireAll"></div>
    	</div>
    </div>
</div>
</template>

<script>
import { mapState } from 'vuex'
export default {
	data (){
		return {
			content: ''
		}
	},
    computed: mapState({
    	data: state => state.bubble
    }),
    methods: {
    	close (){
    		this.data.methods.cancel && this.data.methods.cancel();
    		this.$store.dispatch('bubble_closeBubble');
    	},
    	radioSwitch (event){
    		$('.c-bubble-report .report-content .select-item').removeClass('z-on');
    		$(event.currentTarget).addClass('z-on');
    		this.$store.dispatch('bubble_setState', {
    			report: {
    				type: event.currentTarget.dataset['type']
    			}
    		})
    	},
    	addReport (){
    		this.$store.dispatch('bubble_setState', {
    			report: {
    				content: this.content
    			}
    		}).then( res => {
    			// this.data.report.map[this.data.report.type]
    			return this.$store.dispatch('common_addReport', {
    				"content_id": this.data.report.content_id, //id
					"report_reason": this.data.report.map[this.data.report.type], //
					"content_type": this.data.report.content_type, // 1  2  3 
					"add_reason": this.data.report.content, // 
    			}).then( res => {
    				this.$store.dispatch('bubble_showBubble', {
		                show: false
		            })
		            this.$store.dispatch('bubble_success', res);
    			});
    		}).catch( err => {
    			this.$store.dispatch('bubble_fail', err);
    		})
    	},
    	comfire (){
    		this.data.warn.comfireFn();
    		this.$store.dispatch('bubble_setState', {
    			show: false
    		})
    	},
    	comfireAll (){
    		this.data.methods.comfire();
    		this.$store.dispatch('bubble_setState', {
    			show: false
    		})
    	}
    },
    mounted (){
    	this.$watch('data.outlogin', newVal => {
    		if (newVal) {
				console.log('-----error-----no login, clear user info');
				this.$cache.removeStore(this.$version.userInfo.key);
    			this.$store.dispatch('user_ourlogin');
    		}
    	})
    }
}
</script>




