<!-- style -->
<style lang="less">
@import (reference) '../common/css/common';
    .c-artice-comment {
        width: 100%;
        height: 100%;
        & &-wrap {
            margin-bottom: 30px;
            width: 100%;
            height: 100%;
            .default_backgroud_2;
            .default_border-r-4;
        }
    }
    .c-artice-comment-list {
        .default_border-b-14;
        & .comment__content {
            padding: 20px 30px 20px 20px;
            & .content__top {
                height: 20px;
                margin-bottom: 10px;
                & .top-img {
                    float: left;
                    width: 20px;
                    height: 20px;
                    overflow: hidden;
                    margin-right: 10px;
                    .default_border-r-50;
                    .default_backgroud_5;
                }
                & .top-name {
                    float: left;
                    height: 20px;
                    line-height: 20px;
                    .default_color_3;
                    .default_font_size_3;
                }
            }
            & .content__body {
                padding-left: 30px;
                line-height: 1.5rem;
                margin-bottom: 15px;
                .default_color_2;
                .default_font_size_2;
            }
            & .content__bottom {
                padding-left: 30px;
                height: 12px;
                overflow: hidden;
                .default_color_3;
                .default_font_size_1;
                & .bottom-jubao {
                    float: left;
                    height: 12px;
                    line-height: 12px;
                    .default_pointer;
                    &:hover {
                        .default_color_1;
                    }
                }
                & .bottom-time {
                    float: right;
                    height: 12px;
                    line-height: 12px;
                    margin-right: 15px;
                }
                & .bottom-fabulous {
                    float: right;
                    height: 12px;
                    margin-right: 12px;
                    & span {
                        float: left;
                        display: block;
                        width: 12px;
                        height: 12px;
                        margin-right: 4px;
                        .skin_icon_fabulous;
                        &:hover {
                            .skin_icon_fabulous_on;
                        }
                    }
                    & strong {
                        float: right;
                        height: 12px;
                    }
                    .default_pointer;
                }
                & .bottom-record {
                    float: right;
                    height: 12px;
                    line-height: 12px;
                    .default_color_2;
                    .default_center;
                    .default_pointer;
                    &:hover {
                        .default_color_1;
                    }
                }
            }
        }
        & .comment__record {
            & .record__item {}
            & .record__item-content {
                padding: 10px 30px 10px 50px;
                & .item-text {
                    & p {
                        line-height: 1.5rem;
                        margin-bottom: 15px;
                        .default_color_2;
                        .default_font_size_2;    
                    }
                    & strong {
                        .default_color_6;
                        .default_font_size_1;
                    }
                    & em {
                        font-style: normal;
                    }
                }
                & .item-bottom {
                    overflow: hidden;
                    .default_color_3;
                    .default_font_size_1;
                }
                & .item-bottom-jubao {
                    float: left;
                    .default_pointer;
                    &:hover {
                        .default_color_1;
                    }
                }
                & .item-bottom-time {
                    float: right;
                    margin-right: 15px;
                }
                & .item-bottom-record {
                    float: right;
                    .default_pointer;
                    .default_color_2;
                    &:hover {
                        .default_color_1;
                    }
                }
            }
            & .record__page {
                height: 50px;
                line-height: 50px;
                .default_center;
                .default_font_size_2;
                .default_color_3;
                .default_border-t-14;
                & span {
                    .default_color_2;
                }
                & strong {
                    .default_color_5;
                    .default_pointer;
                }
            }
        }
        & .content__reply {
            height: 80px;
            padding: 20px 30px 20px 50px;
            overflow: hidden;
            & .content__reply-form {
                float: left;
                height: 40px;
                width: 520px;
                margin-right: 10px;
                padding: 0 20px;
                .default_backgroud_4;
                .default_border-r-4;
                .default_color_3;
                .default_font_size_2;
                & input {
                    width: 100%;
                    height: 40px;
                    line-height: 40px;
                    .default_font_size_2;
                    .default_color_3;
                }
            }
            & .content__reply-btn {
                float: left;
                width: 70px;
                height: 40px;
                line-height: 40px;
                text-align: center;
                .default_color_2;
                .default_font_size_2;
                .default_backgroud_5;
                .default_border-r-4;
                .default_pointer;
                & {
                    .default_color_1;
                }
            }
        }
    }
</style>

<!-- html -->
<template>
<div class="c-artice-comment">
<div class="c-artice-comment-wrap">
    <div class="c-artice-comment-list" 
        v-for="(data, _index) in comment.list"
        :data-uid="data.user.uid"
        :data-comment_id="data.comment_id"
    >
        <div class="comment__content">
            <div class="content__top">
                <div class="top-img">
                    <img :src="data.user.avatar_url || avatar" />
                </div>
                <div class="top-name">{{data.user.nick_name}}</div>
            </div>
            <div class="content__body">{{data.comment}}</div>
            <div class="content__bottom">
                <div class="bottom-jubao" @click="addReport"></div>
                <div class="bottom-record" @click="replayCommit(_index, -1)"></div>
                <div class="bottom-time">{{data.publish_time}}</div>
                <div class="bottom-fabulous" @click="addCommentPraise"><span :class="data.hasFabulous ? 'z-on': ''"></span><strong>{{data.praise_count}}</strong></div>
            </div>
        </div>
        <div class="content__reply z-top"
            v-if="data.replayIndex == -1"
        >
            <div class="content__reply-form">
                <input type="text" placeholder="..." v-model="comment_con" />
            </div>
            <div class="content__reply-btn" @click="sendComment(data.comment_id)"></div>
        </div>
        <div class="comment__record">
            <div class="record__item" 
                v-for="(reply, index) in data.replys"
                :data-uid="reply.user.uid"
                :data-comment_id="reply.comment_id"
                :data-index="index"
            >
                <div class="record__item-content">
                    <div class="item-text">
                        <p>
                            <strong>{{reply.user.nick_name}}</strong> 
                            <em v-if="reply.reply_to_user">  <strong>{{reply.reply_to_user.nick_name}}</strong></em>
                            {{reply.comment}}
                        </p>
                    </div>
                    <div class="item-bottom">
                        <div class="item-bottom-jubao" @click="addReport"></div>
                        <div class="item-bottom-record" @click="replayCommit(_index, index)"></div>
                        <div class="item-bottom-time">{{reply.publish_time}}</div>
                    </div>
                </div>
                <div class="content__reply"
                    v-if="index == data.replayIndex"
                >
                    <div class="content__reply-form">
                        <input type="text" placeholder="..." v-model="comment_con" />
                    </div>
                    <div class="content__reply-btn" @click="sendReplyComment(data.comment_id, reply.comment_id)"></div>
                </div>
            </div>
            <div class="record__page" v-if="data.replys && data.reply_page_info.total_count - data.replys.length > 0">
                <span>{{data.reply_page_info.total_count - data.replys.length}}</span> <strong @click="getSubCommentMore(data)"></strong>
            </div>
        </div>
    </div>
    <Empty v-if="comment.count == 0" />
</div>
<Page
    :count="comment.count" 
    :length="pageSize"
    :index="pageIndex"
    @page_switch="pageSwitch"
/>

</div>
</template>

<script>
import {mapState} from 'vuex'
import avatar from '../common/images/img/avatar.png';
export default {
    data () {
        return {
            avatar: avatar,

            pageIndex: 1,
            pageSize: 5,

            commentIndex: 1,
            commentSize: 5,

            comment_con: ''
        }
    },
    computed: mapState({
        userInfo: state => state.user.info,
        comment: state => state.opus.comment,
        catalog: state => state.opus.catalog.info
        // catalog_id: state => state.opus.catalog.catalog_id,
        // chapter_id: state => state.opus.chapter.chapter_id
    }),
    props: ['type'],
    methods: {
        // 
        replayCommit (_index, replayIndex){
            this.$store.dispatch('opus_setCommentList', {
                index: _index,
                replayIndex: replayIndex
            })
        },
        // 
        pageSwitch (pageIndex){
            if (this.pageIndex == pageIndex) {
                return false;
            }
            this.pageIndex = pageIndex;
            this.getCommentList();
        },

        // 
        getCommentList (){
            this.$store.dispatch('opus_getCommentList', {
                "page": this.pageIndex, //,
                "pageSize": this.pageSize, // 10
                "pagination": 1, //1 0   
                "catalogId": this.catalog_id, //id,
                "type": this.type || 2, //   1 (,) 2 () ()
                "chapterId":  this.chapter_id, //id,,,
                "subPageSize": this.commentSize // // 5
            }).then( res => {
                this.$store.dispatch('bubble_success', res);
            }).catch( err => {
                this.$store.dispatch('bubble_fail', err);
            })
        },
        // 
        addComment (payload){
            // 
            if (!this.userInfo.isLogin) {
                return this.$store.dispatch('bubble_fail', {
                    ret: -11,
                    msg: ''
                });
                return false;
            }

            this.$store.dispatch('opus_addComment', {
                "to_comment_id": payload.to_comment_id, //id,(),.
                "to_reply_comment_id": payload.to_reply_comment_id, //id,('xxxxxx',),.
                "comment": payload.comment, //
                "catalog_id": this.catalog_id, //id,
                "chapter_id": payload.chapter_id //id,
            }).then( res => {
                this.$eventHub.$emit('article.comment.refresh');
                this.comment_con = '';
                this.replayIndex = -2;
                this.getCommentList();

                this.$store.dispatch('bubble_success', res);
            }).catch( err => {
                this.$store.dispatch('bubble_fail', err);
            })
        },
        // 
        addCommentPraise (e){
            // 
            if (!this.userInfo.isLogin) {
                return this.$store.dispatch('bubble_fail', {
                    ret: -11,
                    msg: ''
                });
                return false;
            }

            var comment_id = $(e.currentTarget).parents('.c-artice-comment-list').attr('data-comment_id');
            this.$store.dispatch('opus_addCommentPraise', {
                commentId: comment_id
            }).then( res => {
                this.$store.dispatch('bubble_success', res);
            }).catch(err => {
                this.$store.dispatch('bubble_fail', err);
            })
        },
        // 
        addReport (e){
            // 
            if (!this.userInfo.isLogin) {
                return this.$store.dispatch('bubble_fail', {
                    ret: -11,
                    msg: ''
                });
                return false;
            }
            
            var comment_id;
            if ($(e.currentTarget).parents('.record__item').length > 0) {
                comment_id = $(e.currentTarget).parents('.record__item').data('comment_id');
            } else {
                comment_id = $(e.currentTarget).parents('.c-artice-comment-list').data('comment_id');
            }
            this.$store.dispatch('bubble_showBubble', {
                show: true,
                type: 'report',
                report: {
                    content_id: comment_id,
                    content_type: 2
                }
            })
        },
        // 
        sendComment (id){
            this.addComment({
                comment: this.comment_con,
                to_comment_id: id
            })
        },
        // 
        sendReplyComment (comment_id, reply_comment_id){
            this.addComment({
                comment: this.comment_con,
                to_comment_id: comment_id,
                to_reply_comment_id: reply_comment_id
            })
        },
        // 
        getSubCommentMore (data){
            this.commentIndex++;
            this.$store.dispatch('opus_getReplyCommentList', {
                "page": this.commentIndex, //,
                "pageSize": this.commentSize, // 10
                "pagination": 1, //1 0   
                "commentId": data.comment_id //id,
            }).then( res => {
                this.$store.dispatch('bubble_success', res);
            }).catch( err => {
                this.$store.dispatch('bubble_fail', err);
            })
        }
    },
    created () {
        this.$eventHub.$on('article.comment.addComment', payload => {
            this.addComment(payload);
        });
    },
    mounted () {
        // url
        this.catalog_id = this.$url.getUrlParam('catalog_id');
        this.chapter_id = this.$url.getUrlParam('chapter_id');

        // 
        this.getCommentList();
    }
}
</script>




