import {baseUrl, _reject, _rejectObj} from '../config'
import Cookies from 'js-cookie';
// headers
import axios from 'axios'
axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded';
axios.defaults.headers.get['X-Requested-With'] = 'XMLHttpRequest';
var token = Cookies.get('token');

/**
 * 
URL:http://domain/eryuzhisen-server/user/getBlackList
Method:GET
request:
params:
"page":"1",//,
"pageSize":"10",// 10
response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"more":"0",// 0  1 ,
	"list":[
		{
			"uid":"xxxx",
			"nick_name":"xxxxx",//
			"avatar_url":"xxxxx"//url

		}
	]
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getBlackList(option){
	var url = baseUrl + 'user/getBlackList';

	// 
	if (!token) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}

	delete option._header;
	return axios({
		method: 'GET',
		url: url,
		params: option,
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
            return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
	    return _rejectObj(error);
	});
}

/**
 * (header uid)
URL:http://domain/eryuzhisen-server/user/delBlack/{userId}
Method:DELETE
userId:"xxxx"//
response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx"
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function delBlack(option){
	var url = baseUrl + 'user/delBlack/'+option.userId;

	// 
	if (!token) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}

	delete option._header;
	return axios({
		method: 'DELETE',
		url: url,
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
            return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
	    return _rejectObj(error);
	});
}

