/**
 * http://domain/eryuzhisen-server/auth/sigin
Method:POST
request:
body json:
{
	"phone_num":"xxxxx",//
	"password":"xxxx",//
	"device_no":"xxx",//,,
	"pic_vid":"xxxx",//5,
	"pic_vcode":"xxxx",//55,
}

response:
body json:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"uid":"xxxx"//id,
	"token":"xxxx",//7 header
	"device_no":"xxxx",//,request,
	"info_status":"1"// 1: ,
	
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function sigin(option){
	var url = baseUrl + 'auth/sigin';
	var _header = option._header || {};
	delete option._header;
	return axios({
		method: 'POST',
		url: url,
		data: JSON.stringify(option),
	}).then(function (response) {
		if (response.data.ret != 0) {
			return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});
}

/**
 * 
URL:http://domain/eryuzhisen-server/auth/sigup
Method:POST
request:
body json:
{
	"phone_num":"xxxxx",//
	"password":"xxxx",//,:6-12
	"device_no":"xxx",//,,
	"pic_vid":"xxxx",//id
	"pic_vcode":"xxxx",//
	"phone_vcode":"xxxx",//
}

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"uid":"xxxx"//id,
	"token":"xxxx",//7 header
	"device_no":"xxxx",//,request,
	"user":{
		"uid":"xxxx",
		"nick_name":"xxxx",//
		"avatar_url":"xxxx",//url
		"signature":"xxx",//,
		"c_age":"1512",//
	}
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function sigup(option){
	var url = baseUrl + 'auth/sigup';
	var _header = option._header || {};
	delete option._header;

	return axios({
		method: 'POST',
		url: url,
		data: JSON.stringify(option),
		headers: _header
	}).then(function (response) {
		if (response.data.ret != 0) {
			return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
	    return _rejectObj(error);
	});
}

