/**
 * 
URL:http://domain/eryuzhisen-server/user/getUserInfo
Method:GET

request:
params:
userId:"xxxx"//id,headeruid,

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"info":{
		"uid":"xxxx",
		"nick_name":"xxxx",//
		"avatar_url":"xxxxx",//url
		"signature":"xxxx",//
		"c_age":"xxxx",//
		"relation":"0"// 0  1  2  3 
	}
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getUserInfo(option){
	var url = baseUrl + 'user/getUserInfo';
	var _header = option._header || {};

	// 
	if (!token && !option.userId) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}

	delete option._header;
	return axios({
		method: 'GET',
		url: url,
		params: option,
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
			return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
	    return _rejectObj(error);
	});
}

export {
	getPicVerifyCode,
	getPhoneVerifyCode,
	sigin,
	sigup,
	resetPwd,
	unboundPhone,
	reBindPhone,
	updateUserInfo,
	getUserInfo,
	_reject
}





