import axios from 'axios'
import {baseUrl, _reject, _rejectObj} from '../config'
import Cookies from 'js-cookie';
var token = Cookies.get('token');

/**
 * 
URL:http://domain/eryuzhisen-server/user/getMessageList
Method:GET
request:
params:
"page":"1",//,
"pageSize":"10",// 10
"pagination":1,//1 0   
"messageType":"1"// 0  1 , 2 
response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"more":"1",// 0  1 ,
	"page_info":{//pagination1 
			"total_count":"300",//
			"page_count":"30"//	
		},
	"list":[
		{
			"msg_id":"111",//id
			"msg_type":"1",//0  1  2 
			"message":{//
				"sender":{"user_id":"111","nick_name":"xxxx","avatar_url":"xxxx"},//
				"receiver":{"user_id":"111","nick_name":"xxxx","avatar_url":"xxxx"}//
				"content":{
					"title":"xxxx",//:xxx<<xxxx>>
					"cite":"xxxxxxx",//,,
					"content":"xxxxxx",//
					"media_url":"http://xxxxx"//		
				},
				"read":"0",//0  1 
				"attach":{}//,
				"arguments":{"catalog_id","xxxx","chapter_id":"xxxx","to_comment_id":"xxxx","to_reply_comment_id":"xxxxx"},//
			}
			"create_time":"xxx",//
		},
	.....
	]
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getMessageList(option){
	var url = baseUrl + 'user/getMessageList';
	var prosime = axios({
		method: 'GET',
		url: url,
		params: option,
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
            return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

/**
 * 
URL:http://domain/eryuzhisen-server/user/getMessageDetail
Method:GET
request:
params:
"msgId":"1111"//id

response:
{
	"msg_header_icon":"http://xxxx",//
	"msg_id":"111",//id
	"msg_content":"xxx",//
	"msg_media_url":"http://xxxxx",//url
	"create_time":"xxx",//
	"read":"0",//0  1 
	"attach":{}//,
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getMessageDetail(option){
	var url = baseUrl + 'user/getMessageDetail';
	var prosime = axios({
		method: 'GET',
		url: url,
		params: option,
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
            return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

/**
 * 
URL:http://domain/eryuzhisen-server/user/delMessage/{msgId}
Method:DELETE

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx"
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function delMessage(option){
	var url = baseUrl + 'user/delMessage/'+ option.msgId;

	// 
	if (!token) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}

	delete option._header;
	return axios({
		method: 'DELETE',
		url: url,
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
            return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
	    return _rejectObj(error);
	});
}

/**
 * 
URL:http://domain/eryuzhisen-server/user/markMessage/{msgType}/{msgId}
Method:PUT
msgType:1// 0  1 , 2 
msgId:20//id *******-1 ******

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx"
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function markMessage(option){
	var url = baseUrl + 'user/markMessage/'+ option.msgType +'/'+ option.msgId;

	// 
	if (!token) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}

	delete option._header;
	return axios({
		method: 'PUT',
		url: url,
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
            return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
	    return _rejectObj(error);
	});
}

/**
 * 
URL:http://domain/eryuzhisen-server/user/getUnreadMessage
Method:GET

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"list":[
		{
			"message_type":"1",// 0  1 , 2 
			"unread_count":"20",//
		},
		.....
	]
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getUnreadMessage(option){
	var url = baseUrl + 'user/getUnreadMessage';

	// 
	if (!token) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}

	var prosime = axios({
		method: 'GET',
		url: url,
		params: option,
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
            return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

export {
	getMessageList,
	getMessageDetail,
	delMessage,
	markMessage,
	getUnreadMessage,

	_reject,
	_rejectObj
}





