<!-- style -->
<style lang="less">
@import (reference) '../common/css/common';
@module: c-upload;

.@{module} {
    width: 0;
    height: 0;
    opacity: 0;
}
</style>

<!-- html -->
<template>
    <div :id="container" class="c-upload"></div>
</template>

<script>
export default {
    name: 'uploadImg',
    data () {
    	return {
    		accessid: '',
            host: '',
            policyBase64: '',
            signature: '',
            key: '',
            expire: 0,
            uploadfilename: {}
    	}
    },
    props: ['type', 'select', 'container'],
    methods: {
        getParam (cb){
            this.$store.dispatch('common_getPolicyAndAccess', {
                type: this.type || 1
            }).then( (res) => {
                //expire,,.3s 
                this.host = res.host;
                this.policyBase64 = res.policy;
                this.accessid = res.accessid;
                this.signature = res.signature;
                this.expire = parseInt(res.expire);
                this.key = res.dir;

                cb && cb();
                this.$store.dispatch('bubble_success', res);
            }).catch( (err) => {
                this.$store.dispatch('bubble_fail', err);
            })
        },
        random_string (len){
        var len = len || 32;
        var chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678';   
        var maxPos = chars.length;
        var pwd = '';
        for (var i = 0; i < len; i++) {
            pwd += chars.charAt(Math.floor(Math.random() * maxPos));
            }
            return pwd;
        },
        upload (file){
            var imgURL;
            try {  
                //Firefox input[file].value   
                try {  
                    imgURL =  file.getAsDataURL();  
                } catch(e) {  
                    imgURL = window.URL.createObjectURL(file);  
                }
            } catch(e) {  
                var reader = new FileReader();  
                reader.onload = function (e) {  
                    imgURL = e.target.result;  
                };  
                reader.readAsDataURL(file);  
            }
            return imgURL;
        },
        send_request (filename){
            // 
            var pos = filename.lastIndexOf('.')
            var suffix = filename.substring(pos);
            var uploadfilename = this.key + this.random_string(10) + suffix;
            this.uploadfilename[filename] = {
                src: this.host + '/' + uploadfilename,
                load: 0
            };

            this.uploader.setOption({
                'url': this.host,
                'multipart_params': {
                    'key' : uploadfilename,
                    'policy': this.policyBase64,
                    'OSSAccessKeyId': this.accessid, 
                    'signature': this.signature,
                    'success_action_status' : '200', //200,204
                }
            });
            this.uploader.start();
        }
    },
    mounted (){
        var that = this;
        this.getParam();
        this.uploader = new this.$plupload.Uploader({
            runtimes: 'html5, flash, silverlight, html4',
            browse_button: this.select, 
            container: document.getElementById(this.container),
            flash_swf_url: 'lib/plupload-2.1.2/js/Moxie.swf',
            silverlight_xap_url: 'lib/plupload-2.1.2/js/Moxie.xap',
            url: 'https://oss.aliyuncs.com',
            filters: {
                mime_types: [ //zip,rar
                    { 
                        title: "Image files", 
                        extensions: "jpg,gif,png,bmp" 
                    }, 
                ],
                max_file_size: '10mb', //10mb
                prevent_duplicates: true //
            },
            init: {
                // 
                PostInit: function() {
                    // todo
                },
                // 
                FilesAdded: (up, files) => {
                    var now = Date.parse(new Date()) / 1000; 
                    if (this.expire < now + 3) {
                        this.getParam( res => {
                            this.uploader.start();
                        })
                    } else {
                        this.uploader.start();
                    }
                },
                // 
                BeforeUpload: function(up, file) {
                    that.send_request(file.name);
                },
                // 
                UploadProgress: function(up, file) {
                    // todo
                },
                // 
                FileUploaded: (up, file, info) => {
                    var fileUrl = this.uploadfilename[file.name].src;
                    this.uploadfilename[file.name].load = 1;
                    this.$emit('uploadImg_uploaded', fileUrl);
                },
                // 
                Error: (up, err) => {
                    if (err.code == -602) {
                        var fileUrl = this.uploadfilename[err.file.name].src;
                        this.$emit('uploadImg_uploaded', fileUrl);
                    } else {
                        this.$emit('uploadImg_error', err);    
                    }
                }
            }
        });
        this.uploader.init();
    }
}
</script>




