import axios from 'axios'
import {baseUrl, _reject, _rejectObj} from '../config'
import Cookies from 'js-cookie';
var token = Cookies.get('token');

/**
 * banner
ULR:http://domain/eryuzhisen-server/getHomeBanner
Method:GET
response:
body json:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1,
	"errinfo":"xxxx",
	"list":[
		{
		"title":"xxxx",//
		"image_url":"http://xxxx",//
		"jumpui":{
			"page_id":"xx",//id,
			"arguments":{"":""},//,
			}
		},
	....
	]
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getHomeBanner(option){
	var url = baseUrl + 'getHomeBanner';
	var prosime = axios({
		method: 'GET',
		url: url,
		params: option,
	}).then(function (response) {
		if (response.data.ret != 0) {
            return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

/**
 * OSSpolicy
URL:http://domain/eryuzhisen-server/upload/getPolicyAndAccess
Method:GET
request:
params:
"type":"1"//  1  2 

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx",
	"accessid":"xxxx",//accessidaccessid, .
	"policy":"xxxx",//policybase64
	"signature":"xxxxx",//policy
	"dir":"xxx",//
	"host":"xxxx",//.
	"expire":"xxxx"//
}
 * @param  {[type]} option [description]
 * @return {[type]}        [description]
 */
function getPolicyAndAccess(option){
	var url = baseUrl + 'upload/getPolicyAndAccess'; 

	// 
	if (!token) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}
	
	var prosime = axios({
		method: 'GET',
		url: url,
		params: option,
		headers: {
			token: token
		}
	}).then(function (response) {
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

/**
 * 
URL:http://domain/eryuzhisen-server/report/addFeedback
Method:POST
request:
{
	"content":"xxxxxx",//
	"img_url":"http://xxxx",//url
}

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx"
}
 */
function addFeedback(option){
	var url = baseUrl + 'report/addFeedback';

	// 
	if (!token) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}
	
	var prosime = axios({
		method: 'POST',
		url: url,
		data: JSON.stringify(option),
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
            return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

/**
 * 
URL:http://domain/eryuzhisen-server/report/addReport
Method:POST
request:
{
	"report_reason":"xxxx",//
	"content_id":"1111",//id
	"content_type":"1" 1  2  3 
	"add_reason":"xxxxxxxx"//
}

response:
{
	"ret":"1",//0  1 
	"errcode":"10001",// ret1
	"errinfo":"xxxx"
}
 * @param {[type]} option [description]
 */
function addReport(option){
	var url = baseUrl + 'report/addReport';

	// 
	if (!token) {
		return _rejectObj({
			ret: -10,
			msg: ''
		});
	}

	var prosime = axios({
		method: 'POST',
		url: url,
		data: JSON.stringify(option),
		headers: {
			token: token
		}
	}).then(function (response) {
		if (response.data.ret != 0) {
            return _reject(response.data);
		}
		return response.data;
	}).catch(function (error) {
		return _rejectObj(error);
	});

	return prosime;
}

export {
	getHomeBanner,
	getPolicyAndAccess,
	addFeedback,
	addReport,
	_reject,
	_rejectObj
}





