<script>
import { mapState } from 'vuex'
import image from '../common/images/img/catalog.png';
export default {
    data (){
        return {
            show: false,
            tagsshow: false,
            create: false,

            image: image,
            articlename: '',
            control: '',
            control_id: -1,
            oldname: '',
            oldimg: '',
            oldurl: '',
            oldauthor: '',
            share: '',
            share_id: -1,
            articletype: '',
            status: '',
            level: '',
            category_id: -1,
            audit_status: -1,
            level_id: -1,
            tags: [],
            tagsVal: '',
            intro: '',
            agree: true,

            reg_img: true,
            reg_name: true,
            reg_type: true,
            reg_control: true,
            reg_old_name: true,
            reg_old_url: true,
            reg_old_author: true,
            reg_old_img: true,
            reg_share: true,
            reg_status: true,
            reg_level: true,
            reg_tag: true,
            reg_intro: true,
            reg_img_con: '',
            reg_name_con: '',
            reg_control_con: '',
            reg_old_name_con: '',
            reg_old_url_con: '',
            reg_old_img_con: '',
            reg_old_author_con: '',
            reg_share_con: '',
            reg_type_con: '',
            reg_status_con: '',
            reg_level_con: '',
            reg_tag_con: '',
            reg_intro_con: '',

            // 
            count: 8
        }
    },
    props: ['resType'],
    computed: mapState({
        catalog: state => state.opus.catalog,
        category: state => state.opus.category
    }),
    methods: {
        focus (e){
            $(e.currentTarget).parents('.create-top-item').addClass('z-active');
        },
        blur (e){
            $(e.currentTarget).parents('.create-top-item').removeClass('z-active');
        },
        cancel (){
            this.show = false;
        },
        agreeSwitch (e) {
            this.agree = !this.agree;
        },
        selctEnter (e){
            $(e.currentTarget).addClass('z-active');
        },
        selctOut (e) {
            $(e.currentTarget).removeClass('z-active');  
        },
        typeShowValue (e){
            this.articletype = $(e.currentTarget).text();
            this.category_id = $(e.currentTarget).attr('data-id');
            $(e.currentTarget).parents('.create-top-select').removeClass('z-active');
        },
        statusShowValue (e){
            this.status = $(e.currentTarget).text();
            this.audit_status = $(e.currentTarget).attr('data-audit_status');
            $(e.currentTarget).parents('.create-top-select').removeClass('z-active');
        },
        levelShowValue (e){
            this.level = $(e.currentTarget).text();
            this.level_id = $(e.currentTarget).attr('data-level_id');
            $(e.currentTarget).parents('.create-top-select').removeClass('z-active');
        },
        controlShowValue (e){
            this.control = $(e.currentTarget).text();
            this.control_id = Number($(e.currentTarget).attr('data-control_id'));
            $(e.currentTarget).parents('.create-top-select').removeClass('z-active');
        },
        shareShowValue (e){
            this.share = $(e.currentTarget).text();
            this.share_id = Number($(e.currentTarget).attr('data-share_id'));
            $(e.currentTarget).parents('.create-top-select').removeClass('z-active');
        },
        tagsshowOpen (e){
            this.tagsshow = true;
            var node = e.currentTarget;
                node = $(node);
            node.find('input').focus();
            setTimeout(()=>{
                node.find('input').focus();
            }, 20)
        },
        deleteTag (e){
            var index = e.currentTarget.dataset['index'];
                index = Number(index);
            this.tags.splice(index, 1);
            return false;
        },
        enter (e){
            if (this.tags.length >= 3) {
                return false;
            }
            
            this.tagsVal = $.trim(this.tagsVal);
            var text = this.tagsVal;
            if ($.trim(text) == '') {
                this.$store.dispatch('bubble_showBubble', {
                    show: true,
                    type: 'top',
                    top: {
                        status: 'z-warn',
                        msg: ''
                    }
                })
                return false;
            }
            if ($.trim(text).length > 5) {
                this.$store.dispatch('bubble_showBubble', {
                    show: true,
                    type: 'top',
                    top: {
                        status: 'z-warn',
                        msg: '5'
                    }
                })
                return false;
            }
            this.tags.push(text);
            this.tagsVal = '';
        },
        tagsSelect (e){
            var node = $(e.currentTarget);
            if (node.hasClass('z-select')) {
                return false;
            }
            if (this.tags.length >= 3) {
                return false;
            }
            var tag = node.text();
            this.tags.push(tag);
        },
        uploaded (fileUrl){
            // 
            this.image = fileUrl;
        },
        old_uploaded (fileUrl){
            // 
            this.oldimg = fileUrl;
        },
        save (){
            this.reg_img = true;
            this.reg_name = true;
            this.reg_type = true;
            this.reg_control = true;
            this.reg_old_name = true;
            this.reg_old_url = true;
            this.reg_old_author = true;
            this.reg_old_img = true;
            this.reg_share = true;
            this.reg_status = true;
            this.reg_level = true;
            this.reg_tag = true;
            this.reg_intro = true;

            // 
            if (!this.image || this.image == image) {
                this.reg_img = false;
                this.reg_img_con = '';
                return false;
            } else {
                this.reg_img = true;
                this.reg_img_con = '';
            }

            // 
            if (!$.trim(this.articlename)) {
                this.reg_name = false;
                this.reg_name_con = '30';
                return false;
            } else if ($.trim(this.articlename).length > 30) {
                this.reg_name = false;
                this.reg_name_con = '30';
                return false;
            } else {
                this.reg_name = true;
                this.reg_name_con = '';
            }

            // 
            if ($.trim(this.control_id) == -1) {
                this.reg_control = false;
                this.reg_control_con = '';
                return false;
            } else {
                this.reg_control = true;
                this.reg_control_con = '';
            }

            // -
            if ($.trim(this.oldname) == '' && this.control_id == 1) {
                this.reg_old_name = false;
                this.reg_old_name_con = '';
                return false;
            } else {
                this.reg_old_name = true;
                this.reg_old_name_con = '';
            }

            // -
            if ($.trim(this.oldauthor) == '' && this.control_id == 1) {
                this.reg_old_author = false;
                this.reg_old_author_con = '';
                return false;
            } else {
                this.reg_old_author = true;
                this.reg_old_author_con = '';
            }

            // -
            if ($.trim(this.oldurl) == '' && this.control_id == 1) {
                this.reg_old_url = false;
                this.reg_old_url_con = '';
                return false;
            } else {
                this.reg_old_url = true;
                this.reg_old_url_con = '';
            }

            // -
            if ($.trim(this.oldimg) == '' && this.control_id == 1) {
                this.reg_old_img = false;
                this.reg_old_img_con = '';
                return false;
            } else {
                this.reg_old_img = true;
                this.reg_old_img_con = '';
            }

            // 
            if ($.trim(this.share_id) == -1) {
                this.reg_share = false;
                this.reg_share_con = '';
                return false;
            } else {
                this.reg_share = true;
                this.reg_share_con = '';
            }

            // 
            if ($.trim(this.category_id) == -1) {
                this.reg_type = false;
                this.reg_type_con = '';
                return false;
            } else {
                this.reg_type = true;
                this.reg_type_con = '';
            }

            // 
            if ($.trim(this.audit_status) == -1) {
                this.reg_status = false;
                this.reg_status_con = '';
                return false;
            } else {
                this.reg_status = true;
                this.reg_status_con = '';
            }

            // 
            if ($.trim(this.level_id) == -1) {
                this.reg_level = false;
                this.reg_level_con = '';
                return false;
            } else {
                this.reg_level = true;
                this.reg_level_con = '';
            }

            // 
            if (!this.tags.length) {
                this.reg_tag = false;
                this.reg_tag_con = '3';
                return false;
            } else if (this.tags.length > 3) {
                this.reg_tag = false;
                this.reg_tag_con = '3';
                return false;
            } else {
                this.reg_tag = true;
                this.reg_tag_con = '';
            }

            // 
            if (!$.trim(this.intro)) {
                this.reg_intro = false;
                this.reg_intro_con = '1000';
                return false
            } else if ($.trim(this.intro).length > 1000) {
                this.reg_intro = false;
                this.reg_intro_con = '1000';
                return false;
            } else {
                this.reg_intro = true;
                this.reg_intro_con = '';
            }

            // 
            if (!this.agree) {
                this.$store.dispatch('bubble_showBubble', {
                    show: true,
                    type: 'top',
                    top: {
                        status: 'z-warn',
                        msg: ''
                    }
                })
                return false; 
            }

            var that = this;
            this.$store.dispatch('bubble_showBubble', {
                show: true,
                type: 'warn',
                warn: {
                    title: '?',
                    html: '<p></p>' +
                    '<p></p><a href="javascript:void(0);"> </a>',
                    comfireFn: function(){
                        that.addCatalog();
                    }
                },
            })
        },

        addCatalog (){
            var option = {
                "level": this.level_id, // 
                "translate": this.control_id, // 0  1 
                "reprint": this.share_id, // 0  1 
                "category_id": this.category_id, // id
                "catalog_id": this.catalog_id, // id
                "catalog_title": this.articlename, //
                "catalog_desc": this.intro, //
                "catalog_cover_url": this.image, //
                "catalog_status": this.audit_status, //0  1 
                "catalog_label": this.tags.join(',') //
            }
            //  
            if (this.control_id == 1) {
                option.authorize = { 
                    "original_title": this.oldname, // 
                    "original_url": this.oldurl, // 
                    "original_img_url": this.oldimg, // 
                    "original_author": this.oldauthor // 
                }
            }
            this.$store.dispatch(this.create ? 'opus_updateCatalog' : 'opus_addCatalog', option).then( res => {
                var msg;
                if (this.create) {
                    // msg = '';
                    msg = '!';
                } else {
                    msg = '';
                }
                this.$eventHub.$emit('updateCatalog_success');
                this.show = false;

                // 
                this.$store.dispatch('opus_setCatalog_info', option)

                this.$store.dispatch('bubble_success', {
                    ret: 0,
                    type: 'top',
                    msg: msg
                });

                if (this.resType == 'author') {
                    this.refreshCatalog();
                }
            }).catch( err => {
                this.$store.dispatch('bubble_fail', err);
            });
        },

        // 
        getCategoryList (){
            this.$store.dispatch('opus_getCategoryList', {}).then( res => {
                // todo
                
                this.$store.dispatch('bubble_success', res);
            }).catch( err => {
                this.$store.dispatch('bubble_fail', err);
            });
        },
        // 
        getLabelList (number){
            this.$store.dispatch('opus_getLabelList', {
                // categoryId: this.category_id,
                count: number || this.count
            }).then( res => {
                // todo
                
                this.$store.dispatch('bubble_success', res);
            }).catch( err => {
                this.$store.dispatch('bubble_fail', err);
            });
        },
        refresh (){
            this.getLabelList();
        },
        refreshCatalog (){
            this.$eventHub.$emit('author_chapter_list_refresh');
        }
    },
    watch: {
        show (newVal){
            if (newVal) {
                // 
                this.getCategoryList();

                // 
                this.getLabelList();
            }
        }
    },
    updated (){
        var w = 0;
        $(".tag-item").each(function(){
            w += $(this).width() + 10;
        })
        $('.tag-edit').css('padding-left', w + 20);
    },
    mounted (){
        this.$eventHub.$on('getLabelList', option => {
            this.getLabelList(option.number);
        });

        this.$eventHub.$on('header_article_setShow', option => {
            this.show = option.show;

            if (option.catalog) {
                this.create = true;
                this.image = option.catalog.catalog_cover_url;
                this.articlename = option.catalog.catalog_title;
                this.category_id = option.catalog.category_id;
                this.catalog_id = option.catalog.catalog_id;
                this.articletype = option.catalog.category_title;
                this.audit_status = option.catalog.audit_status;
                this.level_id = option.catalog.level;
                this.intro = option.catalog.catalog_desc;
                this.share_id = option.catalog.reprint;
                this.share = option.catalog.reprint == 1 ? '' : '';
                this.control_id = option.catalog.translate;
                this.control = option.catalog.translate == 1 ? '' : '';
                if (option.catalog.authorize) {
                    this.oldname = option.catalog.authorize.original_title; 
                    this.oldauthor = option.catalog.authorize.original_author; 
                    this.oldimg = option.catalog.authorize.original_img_url; 
                    this.oldurl = option.catalog.authorize.original_url;
                }
              
                // 
                this.tags = [];
                option.catalog.labels.map( (item, index) => {
                    this.tags.push(item);
                })

                // 
                for (var i = 0, len = this.catalog.statusArr.length; i < len; i++) {
                    var item = this.catalog.statusArr[i];
                    if (this.audit_status == item.audit_status) {
                        this.status = item.text;
                    }
                }

                //  
                for (var i = 0, len = this.catalog.levelArr.length; i < len; i++) {
                    var item = this.catalog.levelArr[i];
                    if (this.level_id == item.level_id) {
                        this.level = item.text;
                    }
                }
            } else {
                this.create = false;
                this.image = image;
                this.articlename = '';
                this.category_id = -1;
                this.audit_status = -1;
                this.level_id = -1;
                this.intro = '';
                this.articletype = '';
                this.status = '';
                this.level = '';
                this.tags = [];

                this.control = '';
                this.control_id = -1;
                this.oldname = '';
                this.oldimg = '';
                this.oldurl = '';
                this.oldauthor = '';
                this.share = '';
                this.share_id = -1;
            }
        })

        $('body').on('click', e => {
            var node = $(e.target);
            if (!node.hasClass('j-close-1') && node.parents('.j-close-1').length == 0) {
                this.typeShow = false;
                this.statusShow = false;
            }
            if (!node.hasClass('j-close-2') && node.parents('.j-close-2').length == 0) {
                this.tagsshow = false;
                if (this.tags.length == 0) {
                   $('.j-close-2').find('.tag-edit').text('');
                } else {
                    $('.j-close-2').find('.tag-edit').text('');
                }
            }
        })
    }
}
</script>




