<!-- style -->
<style lang="less">
@import (reference) '../common/css/common';
    @module: c-comment-info;

    .@{module} {
        & &-wrap {
            margin-bottom: 30px;
        }
    }
    .@{module}-list {
        padding: 20px 30px 20px 20px;
        .default_border-b-14;
        .default_backgroud_2;
        &.z-unread {
            .default_backgroud_3;
        }
    }
    .@{module}__top {
        height: 20px;
        overflow: hidden;
        margin-bottom: 10px;
        & .top__img {
            float: left;
            width: 20px;
            height: 20px;
            margin-right: 10px;
            .default_border-r-50;
            .default_backgroud_5;
        }
        & .top__name {
            float: left;
            height: 20px;
            line-height: 20px;
            .default_font_size_1;
            .default_color_3;
        }
        & .top__detail {
            float: right;
            height: 20px;
            line-height: 20px;
            & a {
                .default_font_size_1;
                .default_color_3;
            }
        }
    }
    .@{module}__comment {
        padding-left: 30px;
        margin-bottom: 15px;
        .default_color_2;
        .default_font_size_2;
        & .comment__top {
            margin-bottom: 15px;
            .default_color_3;
            & strong {
                .default_color_6;
            }
            & span {
                .default_color_5;
            }
        }
        & .comment__body {
            line-height: 1.5rem;
            margin-bottom: 15px;
        }
        & .comment__bottom {
            line-height: 1.5rem;
            padding: 20px;
            .default_border-r-4;
            .default_color_3;
            .default_font_size_2;
            .default_backgroud_7;
        }
    }
    .@{module}__form {
        padding-left: 30px;
        overflow: hidden;
        & .form__top {
            height: 12px;
            margin-bottom: 15px;
            .default_font_size_1;
            .default_color_3;
        }
        & .form__top__jubao {
            float: left;
            .default_pointer;
            .default_font_size_1;
            .default_color_3;
            &:hover {
                .default_color_1;
            }
        }
        & .form__top__record {
            float: right;
            .default_pointer;
            .default_font_size_1;
            .default_color_2;
            &:hover {
                .default_color_1;
            }
        }
        & .form__body {
            width: 100%;
            overflow: hidden;
        }
        & .form__body__write {
            float: left;
            width: 520px;
            height: 40px;
            .default_backgroud_4;
            & input {
                width: 520px;
                height: 40px;
                line-height: 40px;
                padding: 0 20px;
                .default_color_4;
                .default_font_size_2;    
            }
        }
        & .form_body__commit {
            float: right;
            width: 70px;
            height: 40px;
            line-height: 40px;
            text-align: center;
            .default_border-r-4;
            .default_color_2;
            .default_font_size_2;
            .default_backgroud_5;
            .default_pointer;
            &:hover {
                .default_color_1;
            }
        }
    }
</style>

<!-- html -->
<template>
<div class="c-comment-info">
<div class="c-comment-info-wrap">
    <div class="c-comment-info-list" v-for="(data, index) in message.list" :class="{'z-unread': data.read == 0}">
        <div class="c-comment-info__top">
            <div class="top__img">
                {{data.message.sender.avatar_utl}}
            </div>
            <div class="top__name">{{data.message.sender.nick_name}}</div>
        </div>
        <div class="c-comment-info__comment">
            <div class="comment__top" v-html="data.message.content.title"></div>
            <div class="comment__body">{{data.message.content.content}}</div>
            <div v-if="data.message.content.cite" class="comment__bottom">{{data.message.content.cite}}</div>
        </div>
        <div class="c-comment-info__form">
            <div class="form__top">
                <div class="form__top__jubao" @click="addReport(data.message.sender.user_id)"></div>
                <div @click="recordSwitch(index, data.msg_id, data.read)" class="form__top__record">{{recordIndex == index ? '' : ''}}</div>
            </div>
            <div v-if="recordIndex == index" class="form__body">
                <div class="form__body__write">
                    <input type="text" placeholder="TA..." v-model="comment" />
                </div>
                <div class="form_body__commit" @click="emitComment(data.message.arguments)"></div>
            </div>
        </div>
    </div>
    <Empty 
        class="content-empty" 
        v-if="message.count <= 0"
    />
</div>
<Page 
    :count="message.count" 
    :length="pageSize"
    :index="pageIndex"
    @page_switch="pageSwitch"
/>
</div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    data (){
        return {
            recordIndex: -1,
            pageIndex: 1,
            pageSize: 5,

            comment: '',
            messageType: 2
        }
    },
    computed: mapState({
        message: state => state.message.message
    }),
    methods: {
        recordSwitch (index, msg_id, read){
            if (index == this.recordIndex) {
                this.recordIndex = -1;
            } else {
                this.recordIndex = index;    
            }

            // 
            if (read == 0) {
                this.$store.dispatch('message_markMessage', {
                    msgType: this.messageType,
                    msgId: msg_id
                }).then( res => {
                    this.$eventHub.$emit('getUnreadMessage');
                    this.getMessageList();

                    this.$store.dispatch('bubble_success', res);
                }).catch ( err => {
                    this.$store.dispatch('bubble_fail', err);
                })
            }
        },
        getMessageList (){
            this.$store.dispatch('message_getMessageList', {
                "page": this.pageIndex,
                "pageSize": this.pageSize,
                "pagination": 1,
                "messageType": this.messageType
            }).then( res => {
                this.$store.dispatch('bubble_success', res);
            }).catch( err => {
                this.$store.dispatch('bubble_fail', err);
            });
        },
        pageSwitch (pageIndex){
            if (this.pageIndex == pageIndex) {
                return false;
            }
            this.pageIndex = pageIndex;
            this.getMessageList();
        },
        // 
        addReport (content_id){
            this.$store.dispatch('bubble_showBubble', {
                show: true,
                type: 'report',
                report: {
                    content_id: content_id,
                    content_type: 3
                }
            })
        },
        // 
        getMessageDetail (msgId){
            this.$store.dispatch('message_getMessageDetail', {
                "msgId": msgId
            }).then( res => {
                this.$store.dispatch('bubble_success', res);
            }).catch( err => {
                this.$store.dispatch('bubble_fail', err);
            });
        },
        // 
        addComment (payload){
            this.$store.dispatch('opus_addComment', {
                "to_comment_id": payload.to_comment_id, //id,(),.
                "to_reply_comment_id": payload.to_reply_comment_id, //id,('xxxxxx',),.
                "comment": this.comment, //
                "catalog_id": payload.catalog_id, //id,
                "chapter_id": payload.chapter_id //id,
            }).then( res => {
                this.comment = '';
                this.recordIndex = -2;
                this.getMessageList();

                this.$store.dispatch('bubble_success', res);
            }).catch( err => {
                this.$store.dispatch('bubble_fail', err);
            })
        },
        // 
        emitComment (option){
            if (!option) {
                return false;
            }
            this.addComment({
                "to_reply_comment_id": option.to_comment_id, //id,('xxxxxx',),.
                "comment": this.comment, //
                "catalog_id": option.catalog_id, //id,
                "chapter_id": option.chapter_id //
            })
        }
    },
    mounted (){
        // 
        this.getMessageList();
    }
}
</script>




